  //!save variants
  saveVariants(val) {
    this.modification = true
    console.log(JSON.stringify(val.value.languages))
    let temp = {
      push: val.value.languages
    }
    this.service.postApi('vendor/combination', temp, 0).subscribe(response => {
      if (response['statusCode'] == 200) {
        // this.service.showSuccess('found')
        this.dataLoop = response['result'];
        // console.log('-----------------', this.dataLoop)
        this.myGroup.get('variance').reset()
        for (let i = 0; i < this.dataLoop.length; i++) {
          this.addItem(this.dataLoop[i]);
        }

        // console.log(this.category)
      }
      else {
        this.service.showError('Invalid Email')
      }
    }, error => {
      this.service.showError('Server Error')
    })
  }
  addItem(item): void {
    this.items = this.myGroup.get('variance') as FormArray;
    this.items.push(this.createItem(item));
  }

