  onFileChanged(evt: any) {
    const file = evt.target.files[0];

    if (file) {
      console.log('2222', file);
      const reader = new FileReader();
      reader.onload = this.handleReaderLoaded.bind(this);
      reader.readAsBinaryString(file);
      // console.log(this.base64textString)
    }
  }
  handleReaderLoaded(e) {
    this.imageData.push('data:image/png;base64,' + btoa(e.target.result));
    // this.dataLoop[inde
    // console.log('this is multiple image', this.imageData)
  }
  readThis(evt: any, i) {
    var a = []
    console.log("readthis", typeof evt.target.files['length'])
    for (let index = 0; index < evt.target.files['length']; index++) {
      var file = evt.target.files[index]
      if (file) {
        const reader = new FileReader();
        reader.onload = (file: ProgressEvent) => {

          let url = btoa(file.target['result']);
          a.push('data:image/png;base64,' + url)
          this.myGroup.controls['variance'].controls[i].controls['varianceImage'].setValue(a)
        }
        reader.readAsBinaryString(file);
      }
    }
  }
