import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/Services/provider.service';

@Component({
  selector: 'app-order-list',
  templateUrl: './order-list.component.html',
  styleUrls: ['./order-list.component.css']
})
export class OrderListComponent implements OnInit {
  apiData: any;
  isvalid:Boolean=true
  constructor(private service: DataService) { }

  ngOnInit() {
    this.getOrderList()
    this.service.setTitle('Orders List')
  }


  getOrderList() {
    let temp = {
      lang: "eng"
    }
    this.service.postApi('vendor/vendorOrderList', temp, 1).subscribe(response => {

      if (response['statusCode'] == 200) {
        // this.service.showSuccess("getProduct")
        // console.log("asdfasdf", response['result'])
        this.apiData = response['result'];
        this.isvalid=true
        // this.rows = response['result'];
        // this.temp = [...response['result']];
        console.log('$$$$$$$sdfsdf$$$$$$$$$$$', this.apiData)
        // this.getBrandListdata()
      }
      else {
        // console.log(response['result'])
        this.isvalid=false
        // this.service.showError('NOT FOUND')
        // this.service.showError('Invalid email or password.')
      }
    }, error => {
      console.log('error occur', error)
      this.service.showError('Server Error')
    })
  }


  changeStatus(val) {
    console.log("###",val)
    let temp = {
      status: val,
      lang: "eng"
    }
    console.log("@@@@@@@@@2",temp)
    this.service.postApi('admin/vendorOrderStatus', temp, 1).subscribe(response => {

      if (response['statusCode'] == 200) {
        // this.service.showSuccess("getProduct")
        // console.log("asdfasdf", response['result'])
        this.apiData = response['result'];
        this.isvalid=true
      }
      else {
        // console.log(response['result'])
        this.isvalid=false
        this.service.showError('NOT FOUND')
        // this.service.showError('Invalid email or password.')
      }
    }, error => {
      console.log('error occur', error)
      this.service.showError('Server Error')
    })

  }



  searchData(val) {
    console.log("###",val)
    let temp = {
      searchKeyword: val,
      lang: "eng"
    }
    console.log("@@@@@@@@@2",temp)
    this.service.postApi('vendor/searchVendorOrder', temp, 1).subscribe(response => {

      if (response['statusCode'] == 200) {
        // this.service.showSuccess("getProduct")
        // console.log("asdfasdf", response['result'])
        this.apiData = response['result'];
        this.isvalid=true
      }
      else {
        // console.log(response['result'])
        this.isvalid=false
        // this.service.showError('NOT FOUND')
        // this.service.showError('Invalid email or password.')
      }
    }, error => {
      console.log('error occur', error)
      this.service.showError('Server Error')
    })

  }

}
