import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/Services/provider.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-inventory-listing',
  templateUrl: './inventory-listing.component.html',
  styleUrls: ['./inventory-listing.component.css']
})
export class InventoryListingComponent implements OnInit {
  getProductCount: any;
  apiData: any;

  constructor(public service:DataService, private route:Router) { }

  ngOnInit() {
    this.getproductList()
  }


  getproductList() {
    let temp = {
        lang: "eng"
    }
    this.service.getApi('admin/getProductList', 1).subscribe(response => {
        // console.log("response-======>>>",response)
        if (response['statusCode'] == 200) {
          console.log('===================>>>>',response['result'])
            this.getProductCount = response['result']
            this.getProductCount = this.getProductCount.length
            this.apiData = response['result'];
        }
        else {
            // console.log(response['result'])
            this.getProductCount=0
            this.apiData=false
            console.log('this.apidtaa',this.apiData)
            this.service.showError('NOT FOUND')
            // this.service.showError('Invalid email or password.')
        }
    }, error => {
        console.log('error occur', error)
        this.service.showError('Server Error')
    })
}

variants(val){
  
  // this.route.navigate('/productvariant/?asf='+val)
  this.route.navigate(['/productvariant/'+val]);
  this.service.setTitle('Product Variance')


}
}
