
import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { Observable, Subject, BehaviorSubject } from 'rxjs';
import { ChatAdapter } from 'ng-chat';
@Injectable({
  providedIn: 'root'
})
export class DataService {
  userdata = {}
  nameClicked = { 'userName': '', 'email': '', 'mobileNumber': '' }
  edit_id: any;
  editdata: any;
  optionSubject = new Subject();
  optionObs = this.optionSubject.asObservable();
  constructor(private http: HttpClient, private toastr: ToastrService, private spinner: NgxSpinnerService) {
    // console.log("#############333")
  }
   baseUrl='http://13.126.131.184:5050/' //!live 
  // baseUrl = 'http://192.168.1.35:5050/'
  // baseUrl = 'http://127.0.0.1:5050/'

  // typeLogin: string;
  changeOption(msg) {
    this.optionSubject.next(msg)
  }
  postApi(url, data, isHeader): Observable<any> {
    console.log(`entered in post api `)
    if (isHeader == 0) {
      console.log(`header 0`)
      var httpOptions;
      httpOptions = {
        headers: new HttpHeaders({ "Content-Type": "application/json" })
      }
      return this.http.post((this.baseUrl + url), data, httpOptions)
    }
    else if (isHeader == 1) {
      var httpOptions;
      httpOptions = {

        headers: new HttpHeaders({
          "accessToken": localStorage.getItem("token"),
          // "_id": localStorage.adminId,
          "Content-Type": "application/json"
        })
      }
      return this.http.post((this.baseUrl + url), data, httpOptions)
    }
  }
  getLatApi(address) {
    return this.http.get(`https://maps.google.com/maps/api/geocode/json?address=${address}&sensor=false&key=AIzaSyDxFPSj1Oh9PYbbsm35CLorLQk4vtrTvvk`)
    }
  getApi(url, isHeader) {
    if (isHeader == 0) {
      var httpOptions;
      httpOptions = {
        headers: new HttpHeaders({ "Content-Type": "application/json" })
      }
      return this.http.get((this.baseUrl + url), httpOptions)
    }
    else if (isHeader == 1) {
      // console.log('token', localStorage.token)
      var httpOptions;
      httpOptions = {
        headers: new HttpHeaders({ "accessToken": localStorage.getItem("token"), "Content-Type": "application/json" })
      }
      return this.http.get((this.baseUrl + url), httpOptions)
    }
  }

  showSuccess(msg) {
    this.toastr.success(msg);
  }
  showError(msg) {
    this.toastr.error(msg)
  }
  showBug(msg) {
    this.toastr.info(msg)
  }
  spinnerShow() {
    console.log("asdf")
    this.spinner.show()
  }
  spinnerHide() {
    this.spinner.hide()
  }

  title = new BehaviorSubject('DASHBOARD');

  setTitle(title: string) {
    this.title.next(title);
  }
  // public abstract listFriends(): Observable<User[]>;
  // public abstract getMessageHistory(userId: any): Observable<Message[]>;
  // public abstract sendMessage(message: Message): void;


}