import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/Services/provider.service';

@Component({
    selector: 'app-allproducts',
    templateUrl: './allproducts.component.html',
    styleUrls: ['./allproducts.component.css']
})
export class AllproductsComponent implements OnInit {

    rows = [];
    selected = [];
    temp: any;
    timeout: any;
    public apiData: any;
    getbrandcount: any;
    getCategoryCount: any;
    getProductCount: any;

    constructor(public service: DataService) {
        this.getproductList()
    }

    ngOnInit() {
        this.getBrandList()
        this.getCategory()

        // console.log("this.getproductList()", this.apiData)
        //     console.log('---------->>>', this.rows)
    }

    getproductList() {
        let temp = {
            lang: "eng"
        }
        this.service.getApi('admin/getProductList', 1).subscribe(response => {
            // console.log("response-======>>>",response)
            if (response['statusCode'] == 200) {
                this.getProductCount = response['result']
                // console.log(this.getbrandcount.length)
                this.getProductCount = this.getProductCount.length

                this.apiData = response['result'];
                // this.rows = response['result'];
                // this.temp = [...response['result']];
                // console.log('$$$$$$$sdfsdf$$$$$$$$$$$', this.apiData)
                // this.getBrandListdata()
            }
            else {
                // console.log(response['result'])
                this.getProductCount=0
                this.apiData=false
                console.log('this.apidtaa',this.apiData)
                this.service.showError('NOT FOUND')
                // this.service.showError('Invalid email or password.')
            }
        }, error => {
            console.log('error occur', error)
            this.service.showError('Server Error')
        })
    }

    //getbrandList
    getBrandList() {
        this.service.getApi('admin/getBrandList', 0).subscribe(response => {
            // console.log("response-======>>>",response)
            if (response['statusCode'] == 200) {
                // console.log(response['result'])
                this.getbrandcount = response['result']
                // console.log(this.getbrandcount.length)
                this.getbrandcount = this.getbrandcount.length
            }
            else {
                // console.log(response['result'])
                this.getbrandcount=false
                this.service.showError('NOT FOUND')
                // this.service.showError('Invalid email or password.')
            }
        }, error => {
            console.log('error occur', error)
            this.service.showError('Server Error')
        })
    }
//getCategoryList
getCategory() {
    this.service.getApi('vendor/getCategoryList', 0).subscribe(response => {
        // console.log("response-======>>>",response)
        if (response['statusCode'] == 200) {
            // console.log(response['result'])
            this.getCategoryCount = response['result']
            // console.log(this.getbrandcount.length)
            this.getCategoryCount = this.getCategoryCount.length
        }
        else {
            this.getCategoryCount=0
            // console.log(response['result'])
            this.service.showError('NOT FOUND')
            // this.service.showError('Invalid email or password.')
        }
    }, error => {
        console.log('error occur', error)
        this.service.showError('Server Error')
    })
}


}
