import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/Services/provider.service';
import { DatepickerOptions } from 'ng2-datepicker';
import { IMyDpOptions, IMyDateModel } from 'mydatepicker';
import { FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-create-offer',
  templateUrl: './create-offer.component.html',
  styleUrls: ['./create-offer.component.css']
})
export class CreateOfferComponent implements OnInit {
  public myDatePickerOptions: IMyDpOptions = {
    dateFormat: 'yyyy-mm-dd',
    inline: false,
    showInputField: true,
    showTodayBtn: false,
  };
  public myDatePickerOptions2: IMyDpOptions = {
    dateFormat: 'yyyy-mm-dd',
    inline: false,
    showInputField: true,
    showTodayBtn: false,
  };
  Form: any;
  constructor(private service: DataService, private formBuilder: FormBuilder) {

    this.Form = this.formBuilder.group({
      title: ['', Validators.required],
      Description: ['', Validators.required],
      StartDate: ['', Validators.required],
      EndDate: ['', Validators.required],
      user: ['', Validators.required],
      file: ['', Validators.required]
    })






  }

  ngOnInit() {
    this.onDateChange()
    this.service.setTitle('Create Offer')
  }


  onDateChanged(event: IMyDateModel){
    // console.log(`event${JSON.stringify(event)}`)
    this.Form.value.StartDate
       let endDate = new Date(event.formatted )  
      let copy2 = this.getCopyOfOptions();
      copy2.disableUntil = {
      year: endDate.getFullYear(),
      month: endDate.getMonth() + 1,
      day: endDate.getDate()
      };
      this.myDatePickerOptions2 = copy2 
    }
  onDateChange() {
    let startD = new Date()
    let copy1 = this.getCopyOfOptions();
    copy1.disableUntil = {
      year: startD.getFullYear(),
      month: startD.getMonth() + 1,
      day: startD.getDate()
    };
    this.myDatePickerOptions = copy1

  }
  onDateChanged2(event: IMyDateModel){

  }
  getCopyOfOptions(): IMyDpOptions {
    return JSON.parse(JSON.stringify(this.myDatePickerOptions));
  }
}
