import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { WakiLoginComponent } from './Components/waki-login/waki-login.component';
import { BusinessDetailComponent } from './Components/business-detail/business-detail.component';
import { HomePageVendorComponent } from './Components/home-page-vendor/home-page-vendor.component';
import { AddProductScreenComponent } from './Components/add-product-screen/add-product-screen.component';
import { AddProductComponent } from './Components/add-product/add-product.component';
import { EditproductComponent } from './Components/editproduct/editproduct.component';
import { TestSumitComponent } from './Components/test-sumit/test-sumit.component';
import { ResetPasswordComponent } from './Components/reset-password/reset-password.component';
import { NotFoundComponent } from './Components/not-found/not-found.component';
import { AllproductsComponent } from './Components/allproducts/allproducts.component';
import { PasswordChangedComponent } from './Components/password-changed/password-changed.component';
import { AllordersComponent } from './Components/allorders/allorders.component';
import { ProductdetailComponent } from './Components/productdetail/productdetail.component';
import { InventoryListingComponent } from './Components/inventory-listing/inventory-listing.component';
import { ShowVariantsComponent } from './Components/show-variants/show-variants.component';
import { OrderListComponent } from './Components/order-list/order-list.component';
import { AnalyticsComponent } from './Components/analytics/analytics.component';
import { ActiveOfferComponent } from './Components/active-offer/active-offer.component';
import { CreateOfferComponent } from './Components/create-offer/create-offer.component';
import { LiveViewComponent } from './Components/live-view/live-view.component';
import { RevenueComponent } from './Components/revenue/revenue.component';
import { SalesReportsComponent } from './Components/sales-reports/sales-reports.component';
// import { RevenueComponent } from './Components/revenue/revenue.component';

const routes: Routes = [
  { path: '', redirectTo: 'login', pathMatch: 'full' },
  { path: 'login', component: WakiLoginComponent },
  { path: 'business_details', component: BusinessDetailComponent },
  { path: 'dashBoard', component: HomePageVendorComponent },
  { path: 'addProductScreen', component: AddProductScreenComponent },
  { path: 'addProduct', component: AddProductComponent },
  { path: 'editProduct', component: EditproductComponent },
  { path: 'allproducts', component: AllproductsComponent },
  { path: 'test', component: TestSumitComponent },
  { path: 'resetPassword', component: ResetPasswordComponent },
  { path: 'notFound', component: NotFoundComponent },
  { path: 'passwordChanged', component: PasswordChangedComponent },
  // { path: 'orderlist', component: AllordersComponent },
  { path: 'productdetails', component: ProductdetailComponent },
  { path: 'inventory', component: InventoryListingComponent },
  { path: 'productvariant/:productId', component: ShowVariantsComponent },
  { path: 'orderlist', component: OrderListComponent },
  { path: 'analytics', component: AnalyticsComponent },
  { path: 'activeoffer', component: ActiveOfferComponent },
  { path: 'createoffer', component: CreateOfferComponent },
  { path: 'live', component: LiveViewComponent },
  { path: 'revenue', component: RevenueComponent },
  { path: 'sales-reports', component:SalesReportsComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {

}
