import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { longStackSupport } from 'q';
import { DataService } from 'src/app/Services/provider.service';
declare var $: any
@Component({
  selector: 'app-side-bar',
  templateUrl: './side-bar.component.html',
  styleUrls: ['./side-bar.component.css']
})
export class SideBarComponent implements OnInit {
  isActive: any;
  userdata: any;

  constructor(public route: Router, private service: DataService) {
    // this.getUser() 
  }

  ngOnInit() {
    $("#menu-toggle").click(function (e) {
      e.preventDefault();
      $("#wrapper").toggleClass("toggled");
    });
    
  }
  addProduct() {
    console.log("hgfdfghujhgfds")
    this.route.navigate(['/addProductScreen']);
    this.service.setTitle('Add Product')

  }

  changeStatus(val) {
    this.service.setTitle(val)
  }
  logout() {
    localStorage.clear();
    this.route.navigate(['/login']);
  }

  getUser() {
    this.service.getApi('user/getUserInfo', 1).subscribe(response => {
      // console.log('-----------',response)

      if (response['statusCode'] == 200) {
        this.userdata=response['result']
        console.log("@#$%$#@!@#$#",this.userdata)
      }
      else {
      }
    }, error => {
      console.log('error occur', error)
      this.service.showError('Server Error')
    })
  }

  tabManag(event) {
    console.log(event)
    this.isActive = event;
    let url = window.location.href.split('/')
    let page = url[url.length - 1]
    this.isActive = page;
    this.route.navigate(['/' + event])
  }
}

